.book_section {
  position: relative;

  & .heading_container {
    margin-bottom: 25px;
  }

  & .form_container .form-control {
    width: 100%;
    border: none;
    height: 50px;
    margin-bottom: 25px;
    padding-left: 25px;
    border: 1px solid #999;
    outline: none;
    color: #000000;
    border-radius: 5px;
    outline: none;
    -webkit-box-shadow: none;
    box-shadow: none;
    
    &::-webkit-input-placeholder {
      color: #222222;
    }

    &:-ms-input-placeholder {
      color: #222222;
    }

    &::-ms-input-placeholder {
      color: #222222;
    }

    &::placeholder {
      color: #222222;
    }
  }

  

  & .form_container .nice-select .current {
    font-size: 16px;
  }

  & .form_container button {
    margin-top: 15px;
    border: none;
    text-transform: uppercase;
    display: inline-block;
    padding: 10px 55px;
    background-color: #ffbe33;
    color: #ffffff;
    border-radius: 45px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
    border: none;

    &:hover {
      background-color: #e69c00;
    }
  }

  & .map_container {
    width: 100%;
    height: 345px;
    border-radius: 10px;
    overflow: hidden;
  }

  & .map_container #googleMap {
    height: 100%;
    min-height: 100%;
    width: 100%;
  }
}

